.filter {
    z-index: 10;
    margin: auto;
    width: 100%;
    background-color: #fff;
    position: sticky;
    top: 50px;
    height: 180px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    /*수직 정렬*/
    justify-content: center;
    align-content: center;
    border-bottom: 1px solid gainsboro;
}

.filter .filterlist {
    width: 90%;
    max-width: 1060px;
    display: flex;
    padding-top: 20px;
    padding-bottom: 20px;
    justify-content: space-between;

    border-bottom: 1px solid gainsboro;
}

h1 {
    margin: 0;
    padding: 0;
    border: 0;
}

h2 {
    margin: 0;
    padding: 0;
    border: 0;
}

p {
    margin: 0;
    padding: 0;
    border: 0;
}

a {
    position: relative;
    text-decoration: none;
    vertical-align: middle;
    font-size: 13px;
    line-height: 20px;
    font-weight: 600;
    display: inline-block;
    color: rgb(51, 51, 51);
}

ul {
    list-style: none;
    padding: 0px;
    margin: 0;
}

li {
    display: inline-block;
}

img {
    margin: 0;
    padding: 0;
    border: 0;
}

body {
    margin: 0;
    font-family: Pretendard, Pretendard JP, Pretendard Std, -apple-system, system-ui, BlinkMacSystemFont, Roboto, Segoe UI, Helvetica, Arial,
        Apple SD Gothic Neo, Nanum Gothic, Malgun Gothic, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, sans-serif;
}

header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    background-color: #fff;
    box-shadow: 0 1px 0 0 rgb(0 0 0 / 10%);
    height: 50px;
    align-items: center;
    z-index: 99;
}

header .mainbar {
    width: 87.72%;
    margin-bottom: 0px;
    max-width: 1060px;
    display: flex;
    align-items: center;
    margin: auto;
    justify-content: space-between;
}

.dropdown {
    position: relative;
    display: inline-block;
}
.dropbtn {
    padding: 17px;
    padding-left: 0px;
    margin-right: 25px;
}

.dropdown:hover .dropdown-content {
    display: block;
}

.dropdown-content {
    display: none;
    position: absolute;
    top: 35px;
    left: -10px;
    z-index: 1;
    width: 200px;
    background-color: #fff;
}
.dropdown-content ul {
    overscroll-behavior: contain;
    border: 1px solid gainsboro;
    width: 200px;
    height: calc(70vh - 150px);
    overflow-y: auto;
    overflow-x: hidden;
}
.customizescrollbar::-webkit-scrollbar {
    display: block;
}

.dropdown-content ul:hover::-webkit-scrollbar {
    width: 0px;
    opacity: 0;
    display: none;
}
.dropdown-content li {
    width: 100%;
    display: block;
    font-size: 14px;
    font-weight: 700;
    padding: 0px 10px;
    font-style: normal;
    line-height: 40px;
    letter-spacing: normal;
}

.dropdown-content li:hover {
    background-color: gainsboro;
}

.dropdown-content ul ul {
    background-color: #fff;
    border-left: 1px solid gainsboro;
    display: none;
    width: 200px;
    position: absolute;
    top: 0;
    left: 200px;
}

.dropdown-content li:hover > ul {
    display: block;
}

header .mainbar .left {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
}

header .mainbar img.bar {
    height: 14px;
}

header .mainbar img.logo {
    height: 17px;
}

header .mainbar .center {
    display: flex;
    width: 50%;
    justify-content: space-between;
}

/* header .mainbar .center a {
    padding: 15px;
} */
header .mainbar .center a:hover {
    border-bottom: 1px solid blue;
}
header .mainbar .right {
    display: flex;
    gap: 10px;
    align-items: center;
}

header .mainbar .right a {
    cursor: pointer;
}

header .mainbar .right li {
    cursor: pointer;
    margin-left: 10px;
    font-size: 13px;
    color: #666;
    height: 30px;
    line-height: 30px;
}

#vborder {
    width: 2px;
    height: 10px;
    background-color: rgb(225, 226, 227);
    /* margin: 22px; */
}

.dashboardButton {
    cursor: pointer;
    font-size: 13px;
    color: #666;
    height: 30px;
    line-height: 30px;
    border: 1px solid #e1e2e3;
    border-radius: 15px;
    padding: 0 10px;
    font-weight: 400;
}

.category {
    display: flex;
    width: 90%;
    max-width: 1060px;
    background: #fff;
    padding-top: 80px;
    margin: auto;
}
.message h2 {
    color: #aaa;
    font-weight: 400;
}
.category .joblist {
    display: flex;
    cursor: pointer;
    position: relative;
}

.category .divider {
    border: 1px solid #e1e2e3;
    width: 0px;
    margin: 0 25px;
}

.category .joblist button {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    border: 1px solid #e1e2e3;
    border-radius: 100%;
    background-color: #fff;
    width: 26px;
    height: 26px;
    cursor: pointer;
    transition: 0.3s;
}
.dropdown_joblist {
    display: none;
    position: absolute;
    top: 50px;
    z-index: 11;
    height: 500px;
    width: 150px;
    background-color: #fff;
    border: 1px solid gray;
    overflow-y: auto;
    box-shadow: 0 4px 8px rgb(0 0 0 / 15%);
    border-radius: 5px;
}

.dropdown_joblist a {
    text-align: center;
    display: block;
    font-size: 14px;
    font-weight: 700;
    font-style: normal;
    line-height: 40px;
    letter-spacing: normal;
}

.category .joblist p {
    margin-right: 16px;
    font-size: 24px;
    line-height: 29px;
    font-weight: 700;
    color: #333;
}

.category .joblist img {
    transition: all ease 0.3s;
}

.category .joblist:last-child p {
    font-weight: 400;
}

.filter .filterlist .left {
    display: flex;
    gap: 20px;
}

.filter .filterlist button {
    display: flex;
    align-items: center;
    height: 40px;
    padding: 0 15px;
    border-radius: 5px;
    border: 1px solid #ececec;
    background-color: #fff;
}

.filter .filterlist button p:first-child {
    margin-right: 15px;
    font-size: 14px;
    font-weight: 400 !important;
    letter-spacing: normal;
    color: #333 !important;
}

.filter .filterlist button p:last-child {
    color: #36f;
    font-weight: 600;
    margin-right: 0;
}

.filter .filterlist .right {
    display: flex;
}

.keywordsgradient {
    width: 100px;
    height: 32px;
    right: 0;
    position: absolute;
    background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1));
}
.filter .keywords {
    display: flex;

    position: relative;
    margin: auto;
    overflow: hidden;
    max-width: 1060px;
    width: 90%;
    height: 32px;
}

.filter .keywords .keywordslides {
    position: absolute;
    left: 0;
    top: 0;
    width: 2500px;
    transition: left 0.5s ease-out;
}

.filter .keywords .keywordslides button {
    display: flex;
    position: relative;
    height: 32px;
    padding: 8px 14px;
    border-radius: 20px;
    display: inline-flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    margin-right: 8px;
    font-size: 13px;
    line-height: 16px;
    font-weight: 400;
    color: #333;
    border: none;
}

.filter .keywords .keywordsbutton span {
    font-size: 15px;
    font-weight: 900;
    position: absolute;
    color: black;
    text-align: center;
    border-radius: 100%;
    padding: 5px 8px;
    background-color: #fff;
    border: 1px solid gainsboro;
    cursor: pointer;
}

.filter .keywords .keywordsbutton img {
    width: 12px;
}

.filter .keywords .keywordsbutton span:first-child {
    left: 0;
    display: none;
}

.filter .keywords .keywordsbutton span:last-child {
    right: 0;
}

/* body */
.main {
    display: block;
    margin: 0 auto;
    text-align: center;
    padding-top: 100px;
}

.main .section {
    width: 1060px;
    display: inline-block;
    padding: 60px 0;
}

li {
    list-style-type: none;
}

#slideShow {
    width: 100%;
    height: 300px;
    position: relative;
    margin: auto;
    overflow: hidden;
}

.banner_info {
    position: absolute;
    bottom: 28px;
    width: 330px;
    height: 146px;
    border-radius: 4px;
    background-color: #fff;
    opacity: 1;
    text-align: left;
    left: 25%;
}

.banner_info h2 {
    margin-left: 20px;
    margin-right: 20px;
    font-size: 20px;
    line-height: 1.5;
    margin-top: 20px;
    font-weight: 700;
    color: #333;
    width: 'auto';
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.banner_info h3 {
    margin: 0 20px;
    height: 44px;
    font-size: 14px;
    line-height: 1.64;
    color: #333;

    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.banner_info .banner_divider {
    height: 1px;
    margin: 0;
    background-color: #ececec;
    border: none;
}

.banner_info a {
    margin: 15px 0 0 13px;
    color: blue;
    text-align: center;
}

.banner_info svg {
    width: 1em;
    height: 1em;
    font-size: 11px;
}

.slides {
    position: absolute;
    left: -1080px;
    top: 0;
}

.slides.delay {
    transition: 0.4s ease-out;
}

.slides li:not(:last-child) {
    margin-right: 20px;
}

.controller span {
    position: absolute;
    background-color: transparent;
    color: black;
    text-align: center;
    border-radius: 33%;
    padding: 15px 6px;
    top: 43%;
    background-color: #fff;
    opacity: 0.6;
    cursor: pointer;
    font-size: 25px;
}

.controller img {
    width: 16px;
}

.prev {
    left: 10%;
}

.next {
    right: 10%;
}

.main .section .career {
    margin-bottom: 60px;
}

.main .section .career .title {
    display: flex;
    align-items: center;
    justify-content: center;
}

.main .section .career .title h2 {
    font-size: 18px;
    line-height: 1.33;
    font-weight: 700;
    color: #333;
}

.main .section .career .title button {
    border: none;
    background-color: rgba(0, 0, 0, 0);
}

.main .section .career .scroll_button {
    margin: 20px 0;
    width: 93%;
    display: flex;
    justify-content: space-between;
}

/* 토글 버튼 */
.toggleButton {
    position: absolute;
    border: 1px solid lightgray;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    line-height: 40px;
    border-radius: 100%;
    background-color: white;
}

.select_prev {
    left: 0;
}

.select_next {
    right: 0;
}

.main .section .career .scroll_button .left {
    display: flex;
    position: relative;
    width: 100%;
    margin-right: 10px;
    justify-content: center;
}

.main .section .career .scroll_button .left .select {
    display: flex;
    overflow: hidden;
    width: 97%; /* 슬라이드할 사진과 마진 총 넓이 */
}
.main .section .career .scroll_button .left .select .slide {
    display: flex;
    position: relative;
    gap: 10px;
    width: 2500px;
    transition: left 0.5s ease-out;
}
.main .section .career .scroll_button .left .select .slide li {
    cursor: pointer;
    border: 1px solid lightgray;
    flex-shrink: 0;
    padding: 0 20px;
    font-weight: 600;
    font-size: 15px;
    color: #888;
    height: 40px;
    line-height: 40px;
    border-radius: 15px;
    background-color: white;
}
.main .section .career .scroll_button .left .select .slide li:hover {
    color: blue;
    border: 1px solid blue;
}
.main .section .career .scroll_button .left .select .slide li:first {
    margin-left: 5px;
}
.main .section .career .scroll_button button {
    border: 1px solid lightgray;
    display: block;
    padding: 0 20px;
    font-weight: 600;
    font-size: 15px;
    color: #888;
    height: 40px;
    line-height: 40px;
    border-radius: 15px;
    background-color: white;
}

.main .section .career .scroll_button .left .arrow {
    padding: 10px 12px;
    border-radius: 100%;
}

.main .section .career .scroll_button .right {
    display: flex;
}

.main .section .career .list {
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    text-align: left;
}

.main .section .career .list li {
    display: flex;
    flex-direction: column;
    width: calc(25% - 20px);
    margin: 0 10px 50px;
}

.main .section .career .list h1,
p {
    display: -webkit-box;
    /* 라인수 */
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;

    overflow: hidden;
    /* 생략부호 표시 */
    text-overflow: ellipsis;
}

.main .section .career .list img {
    width: 100%;
    height: 130px;
    border-radius: 4px;
    object-fit: cover;
}

.main .section .career .list h1 {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    color: #333;
}

.main .section .career .list .writer p {
    display: flex;
    align-items: center;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.6666666667em;
    color: #767676;
