.main .section .career .list p img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 1px solid #ececec;
}

.main .section .career .morebutton button {
    height: 50px;
    width: 345px;
    border-radius: 25px;
    font-size: 16px;
    font-weight: 700;
    background-color: #fff;

    border: 1px solid #e1e2e3;
}

.main .section .subsection {
