@media all and (max-width: 1060px) {
    .dashboardButton {
        display: none;
    }
    .category {
        padding-top: 150px;
    }
    .filter {
        top: 110px;
    }
    .filter .filterlist {
        max-width: none;
    }
    .filter .keywords {
        max-width: none;
    }
    .job_main {
        width: 90%;
        padding-top: 20px;
    }
    .job_main .company_list .item {
        max-width: 400px;
        width: 49%;
    }
    .dropdown-content ul {
        width: 100%;
    }
    .dropdown-content ul ul {
        left: 50%;
        width: 50%;
    }
    .dropdown-content ul::-webkit-scrollbar {
        display: none;
    }
}
